import React, {Component, Fragment} from 'react';

export default class ApplicationTableItem extends Component {
    constructor(props) {
        super(props);

        this.state = {
            dropdownActive: false
        }
    }

    render() {
        const type = this.props.type == 1
            ? ''
            : (
                <Fragment>
                     (<a target="_blank" href={process.env.REACT_APP_API_HOST + `uploads/${this.props.photo1}`}>1</a>,
                    <a target="_blank" href={process.env.REACT_APP_API_HOST + `uploads/${this.props.photo2}`}>2</a>)
                </Fragment>
            );

        let identifiers = null;

        if (this.props.registrationNumber) {
            identifiers = this.props.registrationNumber
        }

        if (this.props.vin) {
            identifiers += ', ' + this.props.vin
        }

        if (!identifiers) {
            identifiers = '-'
        }

        return (
            <tr>
                <td>
                    <div className="field has-addons">
                        <div className="control">
                            <a href={this.props.eaistoStatus == "1" ? '' : process.env.REACT_APP_API_HOST + `export/pdf?id=${this.props.id}&type=1`}
                               disabled={this.props.eaistoStatus == "1"}
                               onClick={(e) => {
                                   if (this.props.eaistoStatus == "1") {
                                       e.preventDefault();
                                       return false;
                                   }
                               }}
                               className="button"
                               target="_blank">
                                                <span>
                                                  <i className="fas fa-download"></i>
                                                </span>
                            </a>
                        </div>
                        <div className="control">
                            {/*<Link to={this.props.editable ? `/card/${this.props.id}` : '#'} className="button" disabled={!this.props.editable}>*/}
                            <a href=''
                               className="button"
                               disabled={this.props.eaistoStatus == "2"}
                               onClick={(e) => {
                                   if (this.props.eaistoStatus == "2") {
                                       e.preventDefault();
                                       return false;
                                   }
                               }}>
                                                <span>
                                                  <i className="fas fa-edit"></i>
                                                </span>
                            </a>
                        </div>
                        <div className="control">
                            <div className={this.state.dropdownActive ? "dropdown is-active" : "dropdown"}>
                                <div className="dropdown-trigger">
                                    <button disabled={this.props.eaistoStatus == "1"}
                                            className="button" aria-haspopup="true"
                                            aria-controls="dropdown-menu"
                                            onClick={(e) => {
                                                if (this.props.eaistoStatus == "1") {
                                                    e.preventDefault();
                                                    return false;
                                                } else {
                                                    this.setState({dropdownActive: !this.state.dropdownActive})
                                                }
                                            }}>
                                                        <span className="icon is-small">
                                                        <i className="fas fa-angle-down" aria-hidden="true"></i>
                                                      </span>
                                    </button>
                                </div>
                                <div className="dropdown-menu" id="dropdown-menu" role="menu">
                                    <div className="dropdown-content">
                                        <a href={process.env.REACT_APP_API_HOST + `export/pdf?id=${this.props.id}&type=1`} className="dropdown-item" target="_blank">
                                            
                                        </a>
                                        <a className="dropdown-item" href={process.env.REACT_APP_API_HOST + `export/pdf?id=${this.props.id}&type=2`} target="_blank">
                                              
                                        </a>
                                        <a className="dropdown-item" href={process.env.REACT_APP_API_HOST + `export/pdf?id=${this.props.id}&type=3`} target="_blank">
                                              . 
                                        </a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </td>
                <td>
                    {this.props.createdAt}
                </td>
                <td>
                    {identifiers}
                </td>
                <td>
                    {this.props.email}
                </td>
                <td>
                    {this.props.eaistoNumber ? this.props.eaistoNumber : '-'}
                </td>
                <td>
                    {this.props.purchased ? '' : ''}
                </td>
                <td>
                    {type}
                </td>
                <td>
                    <div className="field has-addons">
                        <div className="control">
                            <a href={process.env.REACT_APP_API_HOST + `api/export/excel`}
                               disabled={this.props.eaistoStatus == "2"}
                               onClick={(e) => {
                                   if (this.props.eaistoStatus == "2") {
                                       e.preventDefault();
                                       return false;
                                   }
                               }}
                               className="button"
                               target="_blank">
                                                <span>
                                                  <i className="fas fa-cloud-upload-alt"></i>
                                                </span>
                            </a>
                        </div>
                        <div className="control">
                            <a href={process.env.REACT_APP_API_HOST + `api/export/excel`}
                               disabled={this.props.eaistoStatus == "2"}
                               onClick={(e) => {
                                   if (this.props.eaistoStatus == "2") {
                                       e.preventDefault();
                                       return false;
                                   }
                               }}
                               className="button"
                               target="_blank">
                                                <span>
                                                  <i className="fas fa-trash-alt"></i>
                                                </span>
                            </a>
                        </div>
                    </div>
                </td>
                <td>
                    <a href={process.env.REACT_APP_API_HOST + `api/export/excel`}
                       className="button"
                       disabled={this.props.eaistoStatus == "1"}
                       onClick={(e) => {
                           if (this.props.eaistoStatus == "1") {
                               e.preventDefault();
                               return false;
                           }
                       }}
                       target="_blank">
                                        <span>
                                          <i className="far fa-envelope"></i>
                                        </span>
                    </a>
                </td>
            </tr>
        )
    }
}



